import React, { Component } from "react";
import { httpClient } from "../../utils/HttpClient";
import { server } from "../../constants/constants";
import ReactApexChart from "react-apexcharts";
import { Link } from "react-router-dom";


class Test_chart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Total_Data: [],
      TTL_series: [],
      TTL_options: {},
      ByCourse_data: [], ByCourse_series: [], ByCourse_options: {},

    }
  }

  componentDidMount = async () => {
    await this.getTotalData();
    await this.getDataBy_Course();

  }

  getTotalData = async () => {
    let Total = await httpClient.get(server.byTTLchart_URL)
    await this.setState({ Total_Data: Total.data.result })
    await console.log(Total.data.result);

    var label_TTL = []
    var data_TTL = [];
    for (let index = 0; index < Total.data.result.length; index++) {
      data_TTL.push(Total.data.result[index].Person);
      label_TTL.push(Total.data.result[index].Person);
    }
    await this.setState({
      // point : output_data.data.result[12].output_accum,
      TTL_series: [
        {
          name: "ACTUAL",
          type: "column",
          data: data_TTL,
        },

      ],
      TTL_options: {
        chart: {
          // height: 350,
          type: "line",
          toolbar: {
            show: false,
          },
        },
        stroke: {
          width: 3,
          curve: 'smooth',
        },
        markers: {
          size: 4,
        },
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: "50%",
            dataLabels: {
              position: 'center',
              orientation: 'vertical',
            }
          },
        },
        title: {
          text: "Total ",
          align: 'center',
          style: {
            fontSize: '25px',
            fontWeight: 'bold',
          }
        },
        dataLabels: {
          enabled: true,
        },
        fill: {
          // opacity: [0.85, 0.25, 1],
          gradient: {
            inverseColors: false,
            shade: "light",
            type: "vertical",
            opacityFrom: 0.85,
            opacityTo: 0.55,
            stops: [0, 100, 100, 100],
          },
        },
        labels: label_TTL,
        xaxis: {
          type: "text",
          title: {
            text: "Person",
            style: {
              fontSize: '15px',
            }
          },

        },
        yaxis: {
          title: {
            text: "Q'ty",
            style: {
              fontSize: '15px',
            }
          },
          min: 0,
          labels: {
            show: true,
          },
        },
        // annotations: {
        //   points: [
        //     {
        //       y: 2000,
        //       label: {
        //         borderColor: '#FF4560',
        //         text: '2000'
        //       }
        //     },

        //   ]
        // }

      },
    })
  }

  getDataBy_Course = async () => {
    let BycourseData = await httpClient.get(server.byCourse_URL)
    //await console.log(BycourseData.data.result);
    await this.setState({ ByCourse_data: BycourseData.data.result })
    await console.log(BycourseData.data.result);

    var label_data = []
    var data_data = [];
    var colors = ["#ff08e2", "#0d1dfc", "#66CC00", "#195529", "#c37e41", "#F4171B", "#ff6900", "#da39fa", "#399dfa", "#dfc800"];
    for (let index = 0; index < BycourseData.data.result.length; index++) {
      data_data.push(BycourseData.data.result[index].Person);
      label_data.push(BycourseData.data.result[index].CourseName);
    }
    await this.setState({
      // point : output_data.data.result[12].output_accum,
      ByCourse_series: [
        {
          name: "person",
          // type: "column",
          data: data_data,
        },

      ],
      ByCourse_options: {
        colors: colors,
        chart: {
          // height: 350,
          type: "bar",
          toolbar: {
            show: false,
          },
        },
        stroke: {
          width: 3,
          curve: 'smooth',
        },
        markers: {
          size: 4,
        },

        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: "50%",
            dataLabels: {
              position: 'center',
              orientation: 'vertical',
            },
            distributed: true,
          },
        },
        title: {
          text: "Classify by Course",
          align: 'center',
          style: {
            fontSize: '25px',
            fontWeight: 'bold',
          }
        },
        dataLabels: {
          enabled: true,
        },
        fill: {
          // opacity: [0.85, 0.25, 1],
          gradient: {
            inverseColors: false,
            shade: "light",
            type: "vertical",
            opacityFrom: 0.85,
            opacityTo: 0.55,
            stops: [0, 100, 100, 100],
          },
        },
        labels: label_data,
        xaxis: {
          type: "text",
          title: {
            text: "Person",
            style: {
              fontSize: '15px',
              colors: "#000000"
            }
          },
          labels: {
            show: false,
          },

        },
        yaxis: {
          title: {
            text: "Q'ty",
            style: {
              fontSize: '15px',
            }
          },
          min: 0,
          labels: {
            show: true,
           
          },
        },
      },
    })
  }
  render() {
    return (
      <div className="content-wrapper">
        <section className="content-header">
          <div className="container-fluid">
            <h2 className="text-center" style={{ fontWeight: 'bold' }} > SUMMARY </h2>
          </div>
        </section>
        <div className="row">
          <div className="card card-warning col-md-12">
            <div className="card-body">
              <div className="row">
                <div className="col-md-6">
                  <ReactApexChart options={this.state.TTL_options} series={this.state.TTL_series} type="line" height={600} />
                </div>
                <div className="col-md-6">
                  <ReactApexChart options={this.state.ByCourse_options} series={this.state.ByCourse_series} type="bar" height={600} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>


    );
  }
}

export default Test_chart;
